import React, {useEffect} from 'react';
import {useNavigate} from 'react-router-dom';

import {getUsersAsAdmin} from './user-management.reducer';
import {useAppDispatch, useAppSelector} from 'app/config/store';
import Table from "@mui/material/Table";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import TableCell from "@mui/material/TableCell";
import TableBody from "@mui/material/TableBody";
import {Avatar, Button, Chip, Stack} from "@mui/material";

export const UserManagement = () => {
  const dispatch = useAppDispatch();
  const navigate = useNavigate();

  const getUsersFromProps = () => {
    dispatch(
      getUsersAsAdmin({
        page: 0,
        size: 9999
      })
    );
  };

  useEffect(() => {
    getUsersFromProps();
  }, []);


  const users = useAppSelector(state => state.userManagement.users);

  return (
    <>
      <Table sx={{minWidth: 650}} aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell>Email</TableCell>
            <TableCell align="right">Login</TableCell>
            <TableCell align="right">Description</TableCell>
            <TableCell align="right">Created</TableCell>
            <TableCell align="right">Authorities</TableCell>
            <TableCell align="right">Avatar</TableCell>
            <TableCell align="right">Action</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {users?.map((user) => (
            <TableRow
              key={user.login}
              sx={{'&:last-child td, &:last-child th': {border: 0}}}
            >
              <TableCell component="th" scope="row">
                <div className="font-bold">
                  {user.email}
                </div>
              </TableCell>
              <TableCell align="right">{user.login}</TableCell>
              <TableCell align="right">{user.description}</TableCell>
              <TableCell align="right">{user.createdDate}</TableCell>
              <TableCell align="right">
                <Stack spacing={0.5}>
                  {user?.authorities?.map(authority => <Chip key={authority} label={authority}/>)}
                </Stack>
              </TableCell>
              <TableCell align="right">
                <div className="flex justify-end">
                  <Avatar
                    alt="Community cover"
                    src={user.imageUrl}
                    sx={{width: 75, height: 75}}
                  />
                </div>
              </TableCell>
              <TableCell align="right">
                <div className="flex flex-col space-y-1">
                  <Button variant="outlined" onClick={() => navigate(`/account/${user.login}`)}>Edit</Button>
                  <Button variant="outlined" onClick={() => navigate(`${user.login}/delete`)} disabled>Delete</Button>
                </div>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </>
  );
};

export default UserManagement;
