import React from 'react';

import {Route} from 'react-router-dom';
import ErrorBoundaryRoutes from 'app/shared/error/error-boundary-routes';
import PageNotFoundRedirect from "app/shared/util/page-not-found-redirect";
import ContentCreation from "app/pages/content-creation/content-creation";
import Notifications from "app/pages/notifications/notifications";
import UserManagement from "app/pages/administration/user-management/user-management";

const AdministrationRoutes = () => (
  <ErrorBoundaryRoutes>
    <Route path="notifications" element={<Notifications/>}/>
    <Route path="user-management/*" element={<UserManagement/>}/>
    <Route path="content-creation" element={<ContentCreation/>}/>
    <Route path="*" element={<PageNotFoundRedirect/>}/>
  </ErrorBoundaryRoutes>
);

export default AdministrationRoutes;
